export default {
  name: "PostComponent",
  data() {
    return {
      error: "",
      text: "",
      mediget: {},
      nodes: [],
      edges: [],
      subject: "chemical",
      predicate: "UMLS:C0004096",
      object: "UMLS:C0812385",
      concept_data: {},
      //concept table
      concepts_table: [],
      selected: [],
      selectTest: [
        {
          index: 0,
          db: "rtx2_2020_09_16",
          id: "MGI:1353598",
          category: "biolink:Gene",
          name: "Mapk8ip3 (mouse)"
        },
        {
          index: 1,
          db: "rtx2_2020_09_16",
          id: "MGI:1353598",
          category: "biolink:Gene",
          name: "Mapk8ip3 (mouse)"
        },        
        {
          index: 2,
          db: "rtx2_2020_09_16",
          id: "NCBIGene:51499",
          category: "biolink:Gene",
          name: "Mapk8ip3 (mouse)"
        },        {
          index: 2,
          db: "rtx2_2020_09_16",
          id: "UMLS:C1417024",
          category: "biolink:NamedThing",
          name: "Mapk8ip3 protein, mouse"
        }
      ],
      shownItems: [],
      select_concepts: [],
      conceptFields: [
        "Selected",

        {
          key: "db",
          label: "Database",
          sortable: true
        },
        {
          key: "id",
          label: "ID"
        },
        {
          key: "category",
          label: "Category",
          sortable: true
        },
        {
          key: "name",
          label: "Name",
          sortable: true
        }
      ],
      predicates: [
        "activator",
        "affects",
        "agonist",
        "allosteric_antagonist",
        "allosteric_modulator",
        "antagonist",
        "blocker",
        "channel_blocker",
        "chemical_or_drug_affects_abnormal_cell",
        "chemical_or_drug_affects_cell_type_or_tissue",
        "chemical_or_drug_affects_gene_product",
        "chemical_or_drug_has_mechanism_of_action",
        "chemical_or_drug_has_physiologic_effect",
        "chemical_or_drug_initiates_biological_process",
        "directly_negatively_regulates",
        "directly_positively_regulates",
        "disrupting_agent",
        "drug_interaction",
        "enzyme_metabolizes_chemical_or_drug",
        "gene_product_has_biochemical_function",
        "gene_product_has_chemical_classification",
        "has_gene_product",
        "ingredient_of",
        "inverse_agonist",
        "INVERTED:biological_process_involves_chemical_or_drug",
        "INVERTED:negatively_regulated_by",
        "INVERTED:process_has_causal_agent",
        "modulator",
        "negative_allosteric_modulator",
        "negative_modulator",
        "positive_allosteric_modulator",
        "positive_modulator",
        "positively_regulates",
        "regulates",
        "regulates_activity_of",
        "regulates_expression_of",
        "regulates_levels_of",
        "target",
        "targets",
        "biolink:positively_regulates",
        "biolink:negatively_regulates"
      ],
      queryResults: [],
      query_fields: [
        {
          key: "subject_name",
          label: "Subject",
          sortable: true,
          tdClass: "colwidth"
        },
        {
          key: "relation",
          label: "Relation",
          sortable: true
        },
        {
          key: "object_name",
          label: "Index",
          sortable: true
        }
      ],
      selectMode: "multi"
    };
  },
  methods: {
    async getQuery_service() {
      try {
        this.queryResults = await PostService.getQuery(this.selectTest);
        console.log(await PostService.getQuery(this.selectTest));
      } catch (err) {
        this.error = err;
      }
    },
    async postQuery_service() {
      try {
        console.log("postQuery_service");
        this.queryResults = await PostService.queryPost(this.selectTest);
        console.log("await PostService.queryPost(this.selectTest)");
        console.log(await PostService.queryPost(this.selectTest));
      } catch (err) {
        this.error = err;
      }
    },
    async getConcept_service() {
      console.log("********* getconcept");
      let i = 0;
      try {
        this.concept_data = await PostService.getConcept("mapk8ip3");
        let set = await PostService.getConcept("mapk8ip3");
        let results = set["concepts"];
        let dbkeys = Object.keys(results);

        // GO THROUGH EACH DATABASE
        dbkeys.forEach(dbkey => {
          let db = results[dbkey];
          let conceptkeys = Object.keys(db);

          conceptkeys.forEach(conceptkey => {
            let concept_data = db[conceptkey];
            // console.log("concept_data")
            // console.log(concept_data)
            let categoriesKept = [
              "biolink:Gene",
              "biolink:MolecularEntity",
              "biolink:NamedThing"
            ];
            try {
              if (categoriesKept.indexOf(concept_data["category"]) !== -1) {
                let concept = {};
                concept.index = i;
                concept.db = dbkey;
                concept.id = conceptkey;
                concept.category = concept_data["category"];
                concept.name = concept_data["name"];
                // if ()
                this.concepts_table.push(concept);
                i++;
              }
            } catch (err) {
              console.log("error at concept - ", err);
            }
          });
        });

        console.log("finshed concept services");
        console.log(this.concepts_table);
      } catch (err) {
        this.error = err;
      }
    },
    // queryParser() {},
    myRowClickHandler(item) {
      for (var i = 0; i < this.concepts_table.length; i++) {
        if (this.concepts_table[i].index === item.index) {
          console.log(item);
          this.select_concepts.push(item);
          this.concepts_table.splice(i, 1);
        }
      }
      // 'record' will be the row data from items
      // `index` will be the visible row number (available in the v-model 'shownItems')
      console.log(item); // This will be the item data for the row
    },
    onRowSelected(items) {
      this.selected = items;
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows();
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected();
    },
    selectThirdRow() {
      // Rows are indexed from 0, so the third row is index 2
      this.$refs.selectableTable.selectRow(2);
    },
    unselectThirdRow() {
      // Rows are indexed from 0, so the third row is index 2
      this.$refs.selectableTable.unselectRow(2);
    }
  }
};
</script>

